.list, .user-page-inner{
  box-shadow: 0px 1px 2px #d8d8d8;
  margin: 10px;
  background: #fff;
}

.item-container{
  min-height: 90px;
  padding: 0 25px;
  position: relative;
}

.item-container:after{
  content: '';
  width: 100%;
  height: 1px;
  background: #f5f5f5;
  margin-top: 0px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 94%;
  right: 0;
  margin: auto;
}

.item-container-avatar{
  display: flex;
  align-items: center;
}

.item-container-name{
  display: flex;
  align-items: center;
  padding-left: 25px;
}

.item-container-name a{
  text-decoration: none;
  color: #000;
}

.item-button{
  box-shadow: none !important;
  background-color: transparent !important;
  border: 1px solid #000 !important;
  border-radius: 25px !important;
  text-transform: none !important;
}

.list-pagination{
  display: flex;
  justify-content: center;
  margin: 25px 0;
}

.list-pagination ul li button{
  border: none;
  background-color: transparent !important;
}

.list-pagination ul li button.Mui-selected{
  border: 1px solid #000;
}

.user-page-inner{
  padding: 15px;
}

.user-image .MuiAvatar-circle{
  width: 100px;
  height: 100px;
  margin-left: 15px;
}

.user-name{
  margin : 0;
  font-size: 32px;
}

.user-location{
  margin: 12px 0px;
  font-weight: 500;
  font-size: 14px;
}

.user-from-date{
  font-size: 14px;
  color: #969696;
}